/* ---------------------------------------------------
  SIDEBAR STYLE
----------------------------------------------------- */
.sidebar {
min-width: 260px;
max-width: 260px;
max-height: 80%;
background: rgb(76, 81, 82); 
color: #fff;
margin-left: -250px;
padding-left: 25px;
transition: all 0.5s;
}

.sidebar.is-open {
margin-left: 0;
transition: .5s;
}


.sidebar-header {
background: rgb(76, 81, 82);
}

.sidebar-header h3{
color: #fff;
padding: 1em;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}

.menu-open {
background: rgb(116, 135, 141);
}

.nav-item:hover {
  color: #052758;
  background: #fff;
}

.items-menu {
color: #fff;
background: rgb(147, 175, 184);
}


.sidebar-header > span {
  position: relative;
  float: right;
  margin: .5em;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}
.side-menu {
height: calc(100vh - 130px);
overflow-y: scroll;
}

.side-menu::-webkit-scrollbar {
width: 10px;
}

.side-menu::-webkit-scrollbar-thumb {
background: #5466B3; 
border-radius: 3px;
}

.side-menu::-webkit-scrollbar-thumb:hover {
background: #5466B3; 
}
li a.dropdown-toggle::after {
  display: inline-flex;
  position: relative;
  left: 10%;
  top: 10%;
}

/* ---------------------------------------------------
  CONTENT STYLE
----------------------------------------------------- */
.content {
padding: 10px;
margin-left: 0;
height: 100vh;
}

@media only screen and (max-width: 500px) {
  body {
    overflow:  hidden;
  }

  .content.is-open {
    margin-left: 100%;
  }

  .sidebar.is-open {
    min-width: 100%;
    max-width: 100%;
    margin-left: 0;
    transition: all 0.5s, height 0s;
  }

  .sidebar.is-open > .sidebar-header span{
    display: unset;
  }

  li a.dropdown-toggle::after {
    display: inline-flex;
    position: relative;
    left: 0%;
    top: 10%;

}



@media screen and (max-width: 700px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar a {float: left;}
  div.content {margin-left: 0;}
}

@media screen and (max-width: 400px) {
  .sidebar a {
    text-align: center;
    float: none;
  }
}

}
